.ratelabel{
    border: 1px solid gray;
    padding: 10px 15px;
    cursor: pointer;
    margin: 5px;
    text-align: left;
}

.rowlabel{
    padding: 10px 15px;
}

/* input[type=radio]{
    display: block;
} */

.row_rating{
    padding: 22px 10px;
    border-bottom: 1px solid #dadce0;
    text-align: center;
}

.corner_text{
    text-align: left !important;
    padding: 10px 15px;
}

.tableclass_rating{
    margin: 40px !important;
    border-collapse: collapse;
    width: -webkit-fill-available;
}

.Sliderbox{
    width: 80%;
    margin: 50px auto;
}

.Slider_Container{
    margin: 25px auto;
    display: flow-root;
    width: 80%;
}

.Slider_label{
    /* border: 1px solid gray; */
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
}

.Slider_box{
    border-bottom: 1px solid #dadce0;
    width: -webkit-fill-available;
    display: flex;
    align-items: center;
    padding: 3px 10px 20px 10px;
}

.RatingSlider_Container{
    margin: 40px 0px 20px 0px;
}