.drag-n-drop {
    padding: .5rem;
    display: grid;
    gap: .5rem;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(auto-fill, 200px);
    align-items: start;
  }
  .dnd-group {
    background-color: #49505e;
    border-radius: 5px;
    padding: .5rem;
  }
  .dnd-item {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    background-color: white;
    border-radius: 5px;
    color: #282c34;
    font-weight: bold;
  }
  .dnd-item:not(:last-of-type) {
    margin-bottom: .5rem;
  }
  .dnd-item * {  
    margin: 0;
    font-size: 1.2rem;
  }
  .group-title {
    text-align: left;
    margin-bottom: .5rem;
    font-size: 1.5rem;
  }
  .current {
    background-color: #282c34;
  }

  .container {
    background-color: #333;
    padding: 1rem 1rem 2rem 1rem;
    margin-top: 1rem;
  }
  
  .draggable {
    padding: 1rem;
    background-color: white;
    border: 1px solid black;
    cursor: move;
  }
  
  .draggable.dragging {
    opacity: .5;
  }

  .RankingDDcontainer{
    padding: 25px 20px 0px 20px !important;
    justify-content: center !important;
  }

  .eUaUj{
    justify-content: center !important;
  }