.idtext{
    display: flex;
    width: -webkit-fill-available;
    justify-content: center;
    padding: 30px 100px;
    font-size: 1.9rem;
    font-weight: 800;
}

.idcontainer{
    display: flex;
    width: -webkit-fill-available;
    justify-content: center;
    padding: 20px 30px 20px 30px;
}

.idinputbox{
    padding: 10px;
    width: 200px !important;
    max-width: inherit;
    border: 0.5px solid gray;
    font-size: 18px;
    border-radius: 8px;
    outline: 0;
}

.EnterIDContainer{
    margin: 50px auto;
    width: fit-content;
    padding: 20px 20px 63px 20px;
    background: #fff;
    border-radius: 10px;
}